}
.item-color.color3[data-v-9a4ed780] {
  background-color: #7f8ea0 !important;
}
.item-color.color4[data-v-9a4ed780] {
  background-color: #89d04f !important;
}
.item-color.color5[data-v-9a4ed780] {
  background-color: #998899 !important;
}
.item-color.color6[data-v-9a4ed780] {
  background-color: #888069 !important;
}
.item-color > p[data-v-9a4ed780] {
