  #nav {
    background-attachment: local;
  }
  #page-header {
    padding: 10px 0.8rem;
  }
}
#pagination {
  overflow: hidden;
  margin-top: 1rem;
  width: 100%;
}
#pagination .pagination {
  text-align: center;
}
#pagination i.fa.fa-chevron-left {
  margin-right: 0.3rem;
  cursor: pointer;
}
#pagination i.fa.fa-chevron-right {
  margin-left: 0.3rem;
  cursor: pointer;
}
#pagination .space {
