  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -moz-box-align: center;
  -o-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  padding: 0 1rem;
  height: 60vh;
}
#site-title,
#site-sub-title {
  color: #eee;
  text-align: center;
  text-shadow: 0.1rem 0.1rem 0.2rem rgba(0, 0, 0, 0.15);
  line-height: 1.5;
}
#site-title {
  font-weight: bold;
  font-size: 1.3rem;
}
#site-sub-title {
  font-size: 0.8rem;
}
#site-social-icons {
  margin: 0 auto;
  width: 15rem;
  text-align: center;
}
#site-social-icons .social-icon {
  margin: 0 0.5rem;
  color: #eee;
  text-shadow: 0.1rem 0.1rem 0.2rem rgba(0, 0, 0, 0.15);
  font-size: 1.4rem;
  cursor: pointer;
}
#site-social-icons .social-icon:hover {
  color: #fff;
}
#page-header {
  position: relative;
  top: 0;
  z-index: 99;
  padding: 10px 36px;
  width: 100%;
  border: none;
  font-size: 18px;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
#page-header .toggle-menu {
  display: none;
  padding-top: 0.5rem;
  color: #eee;
  cursor: pointer;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
#page-header .toggle-menu:hover {
  color: #fff;
}
#page-header a {
  color: #eee;
  text-decoration: none;
}
#page-header a:hover {
  color: #fff;
}
#page-header .site-page {
  position: relative;
  margin-left: 0.6rem;
  padding-bottom: 0.3rem;
  text-shadow: 0.05rem 0.05rem 0.1rem rgba(0, 0, 0, 0.3);
  font-size: 0.7rem;
  cursor: pointer;
}
#page-header .site-page::after {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
  width: 0;
  height: 3px;
  background-color: #80c8f8;
  content: "";
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
#page-header .site-page:hover::after {
  width: 100%;
}
#page-header.fixed {
  position: fixed;
  top: -56px;
  z-index: 101;
  background: rgba(255, 255, 255, 0.8);
  -webkit-box-shadow: 0 5px 6px -5px rgba(133, 133, 133, 0.6);
  box-shadow: 0 5px 6px -5px rgba(133, 133, 133, 0.6);
  -webkit-transition: -webkit-transform 0.2s ease-in-out,
    opacity 0.2s ease-in-out;
  -moz-transition: -moz-transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
  -o-transition: -o-transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
  -ms-transition: -ms-transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
}
#page-header.fixed.open-sidebar .site-page {
  display: none;
  opacity: 0;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: alpha(opacity=0);
}
#page-header.fixed a,
#page-header.fixed #site-name {
  color: #3b3a3a;
  text-shadow: none;
}
#page-header.fixed a:hover,
#page-header.fixed #site-name:hover {
  color: #49b1f5;
}
#page-header.visible {
  -webkit-transform: translate3d(0, 100%, 0);
  -moz-transform: translate3d(0, 100%, 0);
  -o-transform: translate3d(0, 100%, 0);
  -ms-transform: translate3d(0, 100%, 0);
  transform: translate3d(0, 100%, 0);
}
#page-header #site-name {
  text-shadow: 0.1rem 0.1rem 0.2rem rgba(0, 0, 0, 0.15);
  font-weight: bold;
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  #site-title {
    font-size: 2rem;
